import axios from 'axios';

const API = axios.create({ baseURL: 'https://sharecode-71ak.onrender.com' });

API.interceptors.request.use((req) => {
  if (localStorage.getItem('user_info')) {
    req.headers.Authorization = `Bearer ${JSON.parse(localStorage.getItem('user_info')).token}`;
  }

  return req;
});

export const followUser=(id)=>API.patch(`/user/${id}/follow`);


export const fetchUsers = () => API.get(`/user/fetchusers`);
export const fetchPost = (id) => API.get(`/posts/${id}`);
export const fetchPosts = (page) => API.get(`/posts?page=${page}`);
export const fetchPostsByCreator = (name) => API.get(`/posts/creator?name=${name}`);
export const fetchPostsBySearch = (searchQuery) => API.get(`/posts/search?searchQuery=${searchQuery.search || 'none'}&tags=${searchQuery.tags}`);
export const createPost = (newPost) => API.post('/posts', newPost);
export const likePost = (id) => API.patch(`/posts/${id}/likePost`);
export const comment = (value, id) => API.post(`/posts/${id}/commentPost`, { value });
export const updatePost = (id, updatedPost) => API.patch(`/posts/${id}`, updatedPost);
export const deletePost = (id) => API.delete(`/posts/${id}`);

export const signIn = (formData) => API.post('/user/signin', formData);
export const signUp = (formData) => API.post('/user/signup', formData);
export const signInGoogle = (accessToken) => API.post("/user/signin", {
  googleAccessToken: accessToken
})
export const signUpGoogle = (accessToken) => API.post("/user/signup", {
  googleAccessToken:accessToken
})

